@font-face {
	font-family: 'SWRomnd';
	src: url('./ComicSansMS3.ttf');
}
*
{
  font-family: 'SWRomnd', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app
{
  position: relative;
  width: 100%;
  height: 100vh;
  background: url('./img/bg2.jpg') center center no-repeat;
  background-size: cover;

}

.container
{
  width: 80%;
  margin: 0 auto;

}
.mainWindow
{
  width: 1200px;
  margin: 0 auto;
}
.header
{
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background-color: #47FFAC;
}
.mainLink
{
  padding: 0 30px;
  text-decoration: none;
  color: black;
  font-size: 20px;
  border-left: 4px solid black;
}
.mainLink:first-child
{
  border: none;
}
.imgAvatar
{
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 4px solid black;
}
.black__line
{
  width: 100%;
  height: 10px;
  background-color: #000;
}
.mainConteiner
{
  position: relative;
  width: 800px;
  min-height: 650px;
  padding: 40px 30px;
  margin: 0 auto;
  background-color: #fff;

}
.mainText
{
  font-size: 19px;
}
.mainLine
{
  width: 85%;
  border-radius: 4px;
  height: 8px;
  background-color: #000;
  margin: 5px auto;
}
.mainAddBtn
{
  position: absolute;
  bottom: 30px;
  right: 30px;
  background: none;
  border: none;
  cursor: pointer;
}
.btn__block
{
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 200px;
  top: 250px;
  margin: 0 auto;

}


.content__text
{
  width: 400px;
  margin: 0 30px;

}

.login__form
{
  position: relative;
  top: 150px;
  width: 350px;
  margin: 0 auto;
  padding: 30px 20px;
  background-color: #fff;
  border-radius: 15px;
  text-align: center;
}
.inputField
{
  margin: 20px 0;
}
.signup__form
{
  position: relative;
  top: 150px;
  width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 15px;
  text-align: center;
}
.signup__block
{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.signupField
{
  margin: 20px;
}
.addWindow
{
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: rgba(0,0,0,0.6);
}
.addWindowVac
{
  position: relative;
  top: 200px;
  margin: 0 auto;
  padding: 20px 25px;
  width: 750px;
  height: 400px;
  border-radius: 40px;
  border: 2px solid black;
  background-color: #F5F5F5;
}
.mainBtnClose
{
  position: absolute;
  top: 8px;
  left: 690px;
}
.chooseVac
{
  margin-top: 10px;
  padding: 5px 10px ;
  border: 2px solid black;
  border-radius: 5px;
  font-size: 20px;
}
.blockInfoSubtitle
{
  margin-top: 10px;
  font-size: 20px;
}
.blockInfoText
{
  margin-top: 15px;
  font-size: 18px;
}
.blockInfoInput
{
  font-family: 'SWRomnd', sans-serif;
  margin-top: 5px;
  padding: 5px;
  border: 1px solid black;
  border-radius: 10px;
}
.mainBtnAdd
{
  position: absolute;
  right: 20px;
  bottom: 20px;
}
.listVac
{
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  margin-top: 15px;
  background-color: #FFFF66;
  border: 3px solid #BBBF00;
  border-radius: 15px;
}
.arrayVac{
  padding: 15px;
  max-height: 400px;
  overflow-y: auto;
  /* border: 2px solid blue; */
  border-radius:20px;
}
.infoLink
{
  display: block;
  text-decoration: none;
  margin-top: 20px;
  font-size: 20px;
  font-weight: bold;
  color:#000;

}
.punktsMap
{
  display: flex;
  justify-content: space-between;
  align-items: start;
  height: 400px;
  background-color: grey;
  border-radius: 20px;
  margin-top: 30px;
  padding: 20px;
}
.punktsInput
{
  margin-right: 20px;
  padding: 10px;
  border-radius: 10px;
}
.accountBlockFirst
{
  display: flex;
  justify-content: start;

}
.accountAvatar
{
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 4px solid black;
}
.accountMainInfo
{
  padding-top: 20px;
  margin-left: 60px;
}
.accountName
{
  font-size: 30px;
}
.accountEmail
{
  font-size: 25px;
}
.accountBlockSecond
{
  margin-top: 20px;
}
.accountText
{
  font-size: 20px;
  font-weight: bold;
}
.accountSubtext
{
  font-size: 15px;
}
.blueBlock
{
  background-color: #99FFFF;
  border-color: #059BA3;
}
.openUsers
{
  background: none;
  border: none;
  cursor: pointer;
}
.usersMenu
{
  position: absolute;
  width: 250px;
  right: 255px;
  top: 90px;
  background-color: #F5F5F5;
  border: 4px solid black;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;

}
.usersAvatar
{
  width: 80px;
  height: 80px;
  margin: 10px 20px;
}
.usersLine
{
  margin-top: 20px;
  width: 80%;
  height: 2px;
  border-radius: 2px;
  background-color: #000;
  margin: 0 auto;
}
.usersSubtitle
{
  margin-bottom: 10px;
}
.subBlock
{
  margin-top: 40px;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
}
.subUsersBlock
{
  display: flex;
  justify-content: start;
  align-items: center;
  margin: 20px;
}
.subUserAva
{
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
.addNewUser
{
  position: relative;
  bottom: 10px;
  left: -75px;
}
.btnExit
{
  position: absolute;
  right: 20px;
  bottom: 20px;
  border: none;
  border-radius: 10px;
  padding: 10px 15px;
  font-weight: bold;
  background-color: #47FFAC;
  font-style: 17px;
  cursor: pointer;

}
.flowersImg
{
  display: block;
  margin: 0 auto;
  width: 500px;
  height: 500px;
}